/* eslint-disable no-debugger */
/* eslint-disable no-console */
/* global chrome */

function randomNum(minNum, maxNum) {
  switch (arguments.length) {
    case 1:
      return parseInt(Math.random() * minNum + 1, 10);
    case 2:
      return parseInt(Math.random() * (maxNum - minNum + 1) + minNum, 10);
    default:
      return 0;
  }
}

function getSentElement(element_id, tag, parentNode, afterCreate) {
  var element = document.getElementById(element_id);
  if (typeof element === "undefined" || element === null) {
    element = document.createElement(tag);
    element.id = element_id;
    parentNode.appendChild(element)
    afterCreate && afterCreate(element)
  }
  return element
}

const witticismsIndex = randomNum(0, 5)
var wisecrackText = chrome.i18n.getMessage(`blockQuotes_${witticismsIndex}`);
var emojiText = chrome.i18n.getMessage(`blockEmoji_${witticismsIndex}`);
var isFocusing = false;
var keepalive;

function disableKeyboard(e) {
  if (!isFocusing) {
    return true
  }
  e.preventDefault();
  e.stopImmediatePropagation();
  return false
}

function convertTime(time) {
  let totalSec = Math.ceil(time / 1000);
  let sec = totalSec % 60;
  let MIN = parseInt(totalSec / 60);
  MIN = MIN < 10 ? `0${MIN}` : MIN;
  sec = sec < 10 ? `0${sec}` : sec;
  return `${MIN}:${sec}`;
}

function showGiveUpConfirm() {
  var blocker = document.getElementById('tide-blocker')
  blocker.className = 'tide-blocker-giveup-confirm'
}

function hideGiveUpConfirm() {
  var blocker = document.getElementById('tide-blocker')
  blocker.className = ''
}

function closeTab() {
  chrome.runtime.sendMessage({
    type: 'closeCurrentTab'
  });
}

function giveUp() {
  chrome.runtime.sendMessage({
    type: 'stopFocus'
  });
  hideGiveUpConfirm()
  clearTimeout(keepalive);
  disableBlock()
}

function disableBlock() {
  isFocusing = false;
  document.body.className = document.body.className.replace(/tide-blocker-blocked/g, "")
  window.setTimeout(function () {
    let blocker = document.getElementById("tide-blocker");
    if (blocker) {
      blocker.parentNode.removeChild(blocker);
    }
  }, 300)
}

function enableBlock(remain_duration) {
  if (typeof document.body == "undefined" || document.body === null) {
    return
  }

  var blocker = getSentElement('tide-blocker', 'div', document.body)

  var container = getSentElement('tide-blocker-container', 'div', blocker)
  var mask = getSentElement('tide-blocker-mask', 'div', container)
  var box = getSentElement('tide-blocker-main', 'div', container)
  var remain = getSentElement('tide-blocker-remain', 'div', box)
  var wisecrack = getSentElement('tide-blocker-wisecrack', 'div', box)
  var emoji = getSentElement('tide-blocker-wisecrack-emoji', 'div', box)

  var closeBtn = getSentElement('tide-blocker-close-btn', 'button', box, (element) => {
    element.addEventListener("click", closeTab, false)
  })
  var giveupBtn = getSentElement('tide-blocker-giveup-btn', 'button', box, (element) => {
    element.addEventListener("click", showGiveUpConfirm, false)
  })
  container.className = 'tide-blocker-container'
  mask.className = 'tide-blocker-mask'
  box.className = 'tide-blocker-main'
  remain.innerText = convertTime(remain_duration)
  giveupBtn.innerText = chrome.i18n.getMessage('blockGiveupBtn')
  closeBtn.innerText = chrome.i18n.getMessage('blockCloseBtn')
  wisecrack.innerText = wisecrackText
  emoji.innerText = emojiText

  var modal = getSentElement('tide-blocker-modal', 'div', blocker)
  var confirm = getSentElement('tide-blocker-confirm', 'div', modal)
  var confirmText = getSentElement('tide-blocker-confirm-text', 'div', confirm)
  var confirmBtns = getSentElement('tide-blocker-confirm-btns', 'div', confirm)
  var cancelBtn = getSentElement('tide-blocker-confirm-cancel-btn', 'button', confirmBtns, (element) => {
    element.addEventListener("click", hideGiveUpConfirm, false)
  })
  var confirmBtn = getSentElement('tide-blocker-confirm-btn', 'button', confirmBtns, (element) => {
    element.addEventListener("click", giveUp, false)
  })

  confirmText.innerText = chrome.i18n.getMessage('blockConfirmGiveupText')
  cancelBtn.innerText = chrome.i18n.getMessage('blockConfirmGiveupCancelBtn')
  confirmBtn.innerText = chrome.i18n.getMessage('blockConfirmGiveupConfirmBtn')

  const enableBlockClass = 'tide-blocker-blocked'
  const bodyClass = document.body.className
  if (!bodyClass.includes(enableBlockClass)) {
    document.body.className = `${bodyClass} ${enableBlockClass}`.trim()
  }

}

function keepBlockAlive(data) {
  if (data.type === 'focusing') {
    clearTimeout(keepalive);
    enableBlock(data.remain);
    isFocusing = true;
    keepalive = setTimeout(disableBlock, 2e3)
  }
}

window.addEventListener("keydown", disableKeyboard, true);
window.addEventListener("keypress", disableKeyboard, true);
window.addEventListener("keyup", disableKeyboard, true);
chrome.runtime.onMessage.addListener(keepBlockAlive)