/* eslint-disable no-debugger */
/* eslint-disable no-console */
/* global chrome */


(function () {
  "use strict";
  let lastPerformance = 0


  document.addEventListener("visibilitychange", function () {
    if (document.visibilityState === 'visible') {
      lastPerformance = performance.now()
    } else {
      const totalDuration = performance.now()
      const duration = totalDuration - lastPerformance

      chrome.runtime.sendMessage({
        type: 'addViewHistory',
        log: {
          url: location.href,
          duration
        }
      });
    }
  });


  if (['tide-dapi.moreless.io', 'tide-api.moreless.io'].includes(location.hostname) && location.pathname === '/static/login/chrome_ext_cb.html') {
    window.addEventListener('beforeunload', function () {
      const element = document.getElementById('code')
      const code = element.getAttribute('tide-code')
      chrome.runtime.sendMessage({
        type: 'loginWithCode',
        code
      });
    }, false);
  }
})();